                    getState(item?.signals, `15min`) == 'BULLISH'
                      ? 'bullish-changed'
                      : getState(item?.signals, `15min`) == 'BEARISH'
                      ? 'bearish-changed'
                      : 'neutral-changed',
                  ]"
                >
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <span v-bind="attrs" v-on="on">
