                      >
                    </template>
                    <span class="white--text"
                      >Timestamp:
                      {{ getTimeStampToolip(item.updated_at) }}</span
                    >
                  </v-tooltip>
                </span>
              </td>
              <td
                class="text-row"
                style="border-bottom: thin solid hsla(0, 0%, 100%, 0.12)"
                v-else-if="item.type == 'RSI' && rsi && filterColumn('15 Min')"
              >
                <span
                  :class="[
