<script>
import PopUp from "./PopUp.vue";
import ColumnDialog from "./ColumnDialog.vue";
import axios from "axios";
import {
  getSrc,
  getTimeTableRight,
  getColor,
  getState,
  getPrice,
  getColorToChange,
  getColorToScore,
  getTimeStampToolip,
} from "./helper.js";

export default {
  components: {
    PopUp,
    ColumnDialog,
  },
  props: {
    loadingTable: { type: Boolean, required: true },
    items: { type: Array, required: true },
    count: { type: Number },
  },
  data: () => ({
    state_item: "bearish",
    sort_type_item: { text: "Rank", value: "rank" },
    sort_type_items: [
      { text: "Rank", value: "rank" },
      { text: "Technical Score", value: "technical_score" },
      { text: "TREND", value: "TREND" },
      { text: "RSI", value: "RSI" },
      { text: "MACD", value: "MACD" },
    ],
    sort_interval_item: { text: "5Min", value: "5min" },
    sort_interval_items: [
      {
        text: "24H",
        value: "24h",
      },
      {
        text: "4H",
        value: "4h",
      },
      {
        text: "1H",
        value: "1h",
      },
      {
        text: "30Min",
        value: "30min",
      },
      {
        text: "15Min",
        value: "15min",
      },
      {
        text: "5Min",
        value: "5min",
      },
    ],
    search: "",
    oldHeaders: [
      { sortable: false, text: "", value: "symbol_name", width: "16%" },
      { sortable: false, text: "", value: "type" },
      { sortable: false, text: "24H", align: "center" },
      { sortable: false, text: "4H", align: "center" },
      { sortable: false, text: "1H", align: "center" },
      { sortable: false, text: "30 Min", align: "center" },
      { sortable: false, text: "15 Min", align: "center" },
      { sortable: false, text: "5 Min", align: "center" },
    ],
    headers: [
      { sortable: false, text: "", value: "symbol_name", width: "16%" },
      { sortable: false, text: "", value: "type" },
      { sortable: false, text: "24H", align: "center" },
      { sortable: false, text: "4H", align: "center" },
      { sortable: false, text: "1H", align: "center" },
      { sortable: false, text: "30 Min", align: "center" },
      { sortable: false, text: "15 Min", align: "center" },
      { sortable: false, text: "5 Min", align: "center" },
    ],
    check_favorite: false,
    items_favorite: [],
    getSrc,
    getTimeTableRight,
    getColor,
    getState,
    getPrice,
    getColorToChange,
    getColorToScore,
    getTimeStampToolip,
    rsi: true,
    macd: true,
    trend: true,
    listFields: [
      {
        text: "24H",
        value: "24h",
      },
      {
        text: "4H",
        value: "4h",
      },
      {
        text: "1H",
        value: "1h",
      },
      {
        text: "30 Min",
        value: "30 min",
      },
      {
        text: "15 Min",
        value: "15 min",
      },
      {
        text: "5 Min",
        value: "5 min",
      },
    ],
    selectedHeaders: ["24H", "4H", "1H", "30 Min", "15 Min", "5 Min"],
  }),
  watch: {
    selectedHeaders: {
      handler(value) {
        const temp1 = value.map((x) => x);
        let temp2 = this.oldHeaders.map((x) => {
          return x.text;
        });
        temp2 = temp2.filter((x) => x);
        if (this.arraysEqual(temp1, temp2)) this.headers = this.oldHeaders;
        else {
          this.headers = this.headers.filter((x) => {
            if (x.text.length > 0) return value.includes(x.text);
            else return true;
          });
        }
      },
    },
  },
  computed: {
    itemsSort() {
      let temp = [...this.items];
      if (
        ["TREND", "RSI", "MACD"].some((x) => x == this.sort_type_item.value)
      ) {
        temp = temp.sort((a, b) => {
          if (
            a.type == this.sort_type_item.value &&
            a.signals &&
            a.signals.includes(
              `"interval":"${this.sort_interval_item.value}","state":"${this.state_item}"`
            )
          ) {
            return -1;
          }
          if (
            b.type == this.sort_type_item.value &&
            b.signals &&
            b.signals.includes(
              `"interval":"${this.sort_interval_item.value}","state":"${this.state_item}"`
            )
          ) {
            return 1;
          }
          return 0;
        });
      } else {
        temp = temp.sort((a, b) => {
          switch (this.sort_type_item.value) {
            case "technical_score":
              if (this.state_item == "bearish") {
                if (a.technical_score > b.technical_score) return -1;
                if (a.technical_score < b.technical_score) return 1;
                return 0;
              } else {
                if (a.technical_score > b.technical_score) return 11;
                if (a.technical_score < b.technical_score) return -1;
                return 0;
              }

            case "rank":
              if (this.state_item == "bearish") {
                if (a.rank > b.rank) return 1;
                if (a.rank < b.rank) return -1;
                return 0;
              } else {
                if (a.rank > b.rank) return -1;
                if (a.rank < b.rank) return 1;
                return 0;
              }
          }
        });
      }
      temp = temp.reduce(function (accumulator, currentValue) {
        if (!accumulator[`${currentValue.symbol_name}_TREND`]) {
          accumulator[`${currentValue.symbol_name}_TREND`] = {
            id: currentValue.id,
            rank: currentValue.rank,
            coin_price: currentValue.coin_price,
            coin_symbol: currentValue.coin_symbol,
            symbol_name: currentValue.symbol_name,
            type: "TREND",
            created_at: new Date(Date.UTC(null)),
            updated_at: new Date(Date.UTC(null)),
          };
        }
        if (!accumulator[`${currentValue.symbol_name}_MACD`]) {
          accumulator[`${currentValue.symbol_name}_MACD`] = {
            id: currentValue.id,
            rank: currentValue.rank,
            coin_price: currentValue.coin_price,
            coin_symbol: currentValue.coin_symbol,
            symbol_name: currentValue.symbol_name,
            type: "MACD",
            created_at: new Date(Date.UTC(null)),
            updated_at: new Date(Date.UTC(null)),
          };
        }
        if (!accumulator[`${currentValue.symbol_name}_RSI`]) {
          accumulator[`${currentValue.symbol_name}_RSI`] = {
            id: currentValue.id,
            rank: currentValue.rank,
            coin_price: currentValue.coin_price,
            coin_symbol: currentValue.coin_symbol,
            symbol_name: currentValue.symbol_name,
            type: "RSI",
            created_at: new Date(Date.UTC(null)),
            updated_at: new Date(Date.UTC(null)),
          };
        }
        if (accumulator[`${currentValue.symbol_name}_${currentValue.type}`]) {
          accumulator[`${currentValue.symbol_name}_${currentValue.type}`] =
            new Date(
              accumulator[
                `${currentValue.symbol_name}_${currentValue.type}`
              ].updated_at
            ) < new Date(currentValue.updated_at)
              ? currentValue
              : accumulator[`${currentValue.symbol_name}_${currentValue.type}`];
        }
        return accumulator;
      }, {});
      temp = Object.keys(temp).map(function (key) {
        return temp[key];
      });
      return temp.filter((item) => {
        return (
          item &&
          (item.symbol_name
            .toString()
            .toLowerCase()
            .includes(this.search.toLowerCase().trim()) ||
            item.coin_symbol
              .toString()
              .toLowerCase()
              .includes(this.search.toLowerCase().trim()))
        );
      });
    },
  },
  methods: {
    filterColumn(text) {
      if (this.headers.find((x) => x.text == text)) return true;
      return false;
    },
    showPopup(item) {
      const data = axios.post(
        "https://app.fidata.pro/api/quantifycrypto-coin",
        {
          symbol: item,
        }
      );
      this.$refs.formCoin.showDialog(data.data);
    },
    onFilterTREND() {
      this.trend = !this.trend;
    },
    onFilterRSI() {
      this.rsi = !this.rsi;
    },
    onFilterMACD() {
      this.macd = !this.macd;
    },
    onFilterColumn() {
      this.$refs.columnDialog.open();
    },
    reset(value) {
      this.selectedHeaders = value;
    },
    arraysEqual(a1, a2) {
      /* WARNING: arrays must not contain {objects} or behavior may be undefined */
      return JSON.stringify(a1) == JSON.stringify(a2);
    },
    checkFavorite({ coin_name }) {
      const list_coin = localStorage.getItem("COIN_FAVORITE");
      if (!list_coin) return false;
      return JSON.parse(list_coin).includes(coin_name);
    },
    clickFavorite() {
      this.check_favorite = !this.check_favorite;
      let items = this.items;
      if (this.check_favorite) {
        const check_list_coin = localStorage.getItem("COIN_FAVORITE");
        if (!check_list_coin) return;
        const list_coin = JSON.parse(check_list_coin);
        this.items_favorite = items.filter((x) =>
          list_coin.includes(x.coin_name)
        );
      } else {
        this.items_favorite = items;
      }
      this.$emit("filter:items-favorite", this.items_favorite);
    },
    addFavorite({ coin_name }) {
      const list_coin = localStorage.getItem("COIN_FAVORITE");
      if (!list_coin) {
        localStorage.setItem("COIN_FAVORITE", JSON.stringify([coin_name]));
      } else {
        let arr = JSON.parse(list_coin);
        if (arr.includes(coin_name)) {
          arr = arr.filter((x) => x != coin_name);
        } else {
          arr.unshift(coin_name);
        }
        localStorage.setItem("COIN_FAVORITE", JSON.stringify(arr));
        this.$emit("fetch-data");
      }
    },
  },
};
</script>
